:root {
  --color-primary: #2584ff;
  --color-secondary: #00d9ff;
  --color-accent: #ff3400;
  --color-headings: #1b0760;
  --color-body: #918ca4;
}

html {
  font-size: 62.5%;
}

h1, h2, h3 {
  color: var(--color-headings);
  margin-bottom: 1rem;
}

h1 {
  font-size: 7rem;
}

h3 {
  font-size: 3rem;
}

p {
  margin-top: 0;
}

body {
  color: var(--color-body);
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-size: 2.4rem;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

.link-arrow {
  color: var(--color-accent);
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bold;
}

.link-arrow:after {
  content: "-->";
  margin-left: 5px;
  transition: margin .15s;
}

.link-arrow:hover:after {
  margin-left: 10px;
}

.badge {
  white-space: nowrap;
  color: #fff;
  border-radius: 2rem;
  padding: .5rem 2rem;
  font-size: 2rem;
  font-weight: 600;
}

.badge-primary {
  background-color: #126de4;
}

.badge-secondary {
  background-color: #02cdf1;
}

.badge-small {
  font-size: 1.6rem;
}

.list {
  color: var(--color-headings);
  padding-left: 0;
  font-size: 2.4rem;
  list-style-type: none;
}

.list-inline .list_item {
  margin-right: 2rem;
  display: block;
}

.list-tick {
  padding-left: 3rem;
  list-style-image: url("tick.09ba787a.svg");
}

.list-tick .list_item {
  margin-bottom: 1rem;
  padding-left: .5rem;
}

.icon {
  width: 5rem;
  height: 5rem;
}

.icon-primery {
  fill: var(--color-primary);
  padding: .5rem;
}

.icon-container {
  width: 6.4rem;
  height: 6.4rem;
  background-color: #f3f9fa;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

*, :after, :before {
  box-sizing: border-box;
}

.btn {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  white-space: nowrap;
  border: none;
  border-radius: 5rem;
  outline: none;
  margin: .5rem 0;
  padding: 2rem 4vw;
  font-size: 1.8rem;
  font-weight: 600;
}

.btn-secondary {
  background-color: var(--color-secondary);
}

.btn .btn-search {
  fill: #fff;
  height: 2rem;
  width: 2rem;
  vertical-align: middle;
  margin-right: 1rem;
}

.btn-accent {
  background-color: var(--color-accent);
}

.btn-border {
  border: .2rem solid var(--color-headings);
  color: var(--color-headings);
  background-color: #fff;
}

.btn-block {
  width: 0%;
  flex-grow: 1;
  font-size: 1.5rem;
}

.button-btn {
  width: 100%;
  display: flex;
}

.btn-border:hover {
  background: var(--color-headings);
  color: #fff;
}

.btn-accent:hover {
  background-color: #fa491b;
}

.btn-secondary:hover {
  background-color: #4ae2fd;
}

.input {
  width: 100vw;
  color: var(--color-headings);
  border: .1rem solid #d3d3d3;
  border-radius: 5rem;
  outline: none;
  padding: 2rem 4rem;
  font-size: 2rem;
}

::placeholder {
  color: #cdcbd7;
  padding-left: 3rem;
}

.input-group {
  width: 100%;
  border: none;
  padding: 0 .5rem;
  display: flex;
  box-shadow: 0 0 30px 10px #d3d3d3;
}

.btn-input {
  border: none;
  border-radius: 5rem;
  outline: none;
  padding-left: 3rem;
  font-size: 1.5rem;
}

.card {
  width: auto;
  flex-direction: column;
  padding: 4rem;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 50px 20px gray;
}

.card .list-tick {
  margin: 0;
  padding: 4rem;
}

.card .btn-border {
  margin: 2rem;
}

.card-header {
  background-color: var(--color-secondary);
  color: #fff;
  padding: 3rem 3.5rem 1rem;
}

.card .card-primary {
  background-color: var(--color-primary);
}

.card-header {
  position: relative;
}

.card-primary:before {
  content: url("popular.ca183884.svg");
  width: 35px;
  display: inline-block;
  position: absolute;
  top: 0;
  right: 5%;
}

.card .list-tick {
  font-weight: 500;
}

.month {
  position: absolute;
  top: 4rem;
  left: 10rem;
}

.pbottom {
  vertical-align: bottom;
  justify-content: left;
  margin-bottom: 0;
  position: relative;
}

.big {
  margin-bottom: 0;
  font-size: 6rem;
}

.pbold {
  justify-content: left;
  margin-bottom: 0;
  font-weight: 600;
}

.psmall {
  justify-content: left;
  font-size: 2rem;
}

.card-badge {
  text-transform: uppercase;
  margin-left: 2rem;
  font-size: 1.6rem;
  position: absolute;
  top: 2rem;
  left: 18rem;
}

.grid {
  display: grid;
}

.media {
  width: 100%;
  padding-bottom: 8rem;
  display: flex;
}

.media-title {
  color: #fff;
  margin-top: 0;
  font-size: 2.4rem;
}

.media-image {
  margin-top: 1rem;
}

.image-author {
  align-self: flex-end;
}

.media-image .icon-primery {
  fill: #02cdf1;
}

.mediasection {
  padding: 0 2rem;
}

.showcasep {
  text-align: left;
  opacity: .4;
  font-size: 2rem;
}

.media-bodi {
  margin-left: 2rem;
}

.quotes-author {
  width: 100%;
  display: flex;
}

.media-total {
  width: 0%;
  flex-grow: 1;
}

.media-bodi-testimonials {
  width: 0;
  flex-grow: 1;
}

.quotes-paragraph {
  color: #5c5577;
  font-size: 3rem;
  font-style: italic;
}

.author {
  color: var(--color-headings);
  margin-bottom: 0;
  padding-left: 0;
  font-size: 3rem;
  font-weight: 700;
}

.company {
  color: var(--color-body);
  opacity: .4;
  justify-content: left;
  padding-left: 7rem;
  font-size: 2rem;
}

.quotes-paragraph:before {
  content: open-quote;
}

.quotes-paragraph:after {
  content: close-quote;
}

.media-bodi {
  color: var(--color-body);
  text-align: left;
}

.media-bodis {
  justify-content: left;
}

.testimonials {
  width: 100%;
  border-radius: 1rem;
  flex-direction: column;
  margin: 2.5rem 2rem;
  padding: 2rem;
  display: flex;
  position: relative;
  box-shadow: 0 0 20px 10px #d3d3d3;
}

.block-testimonials {
  padding-bottom: 20rem;
}

.testimonials-image {
  width: 100%;
}

.testimonials-img {
  padding: 3rem;
  position: relative;
}

.testimonials-img .icon-container-accent {
  background-color: var(--color-accent);
  fill: #fff;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  padding: 1.8rem;
  position: absolute;
  top: 5rem;
  right: -2px;
}

.media-bodis {
  color: var(--color-body);
  margin-bottom: 0;
}

.image-author {
  margin-top: -.5rem;
}

.block__header .block__heading-testimonials {
  text-align: center;
}

.featuresblck {
  padding: 2rem;
}

.callout {
  background-color: var(--color-primary);
  border-radius: 1rem;
  flex-direction: column;
  margin: 6rem;
  padding: 6rem;
  display: flex;
  position: absolute;
}

.callout-text > h3, p {
  color: #fff;
  text-align: center;
  justify-content: center;
  align-self: center;
  display: flex;
}

.callout-text > h3 {
  margin-top: 0;
  font-size: 4rem;
}

.callout > button {
  width: 0;
  align-self: center;
  padding: 3rem;
  font-size: 1.8rem;
}

.collapsible {
  padding: 2rem;
}

.coll-header {
  position: relative;
}

.posledendel {
  border-bottom: .1rem solid gray;
}

.collapsible-header {
  color: #fff;
  margin-top: 0;
  font-size: 2rem;
}

.collapsible-body {
  opacity: 0;
  max-height: 0;
  padding-top: 1.5rem;
  font-size: 2.4rem;
  transition: all .3s;
  display: none;
  overflow: hidden;
}

.chevron {
  width: 6rem;
  transition: all .3s;
  position: absolute;
  right: 0;
  transform: rotate(-90deg);
}

.collapsible--expanded .chevron {
  transform: rotate(0);
}

.collapsible--expanded .collapsible-body {
  max-height: 100vh;
  opacity: .4;
  display: inline-block;
}

.navigationlist {
  background-color: #000;
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.navlist_item {
  color: #f5f5f5b6;
  border-bottom: 1px solid #a9a9a9;
  padding: .5rem;
}

.navnav {
  width: 100%;
  margin: 0;
}

.navicon {
  fill: #f5f5f5b6;
}

.navlist_item > a {
  color: #d2cfd9;
  opacity: .9;
  margin-top: 2rem;
}

.navlist_item > a:hover {
  color: #fff;
  opacity: 1;
  transition: color .3s;
}

.navigationlist .collapsible {
  padding: 0;
}

.collapsible--expanded .navicon {
  fill: #fff;
  cursor: pointer;
  border-radius: .5rem;
  padding: 0;
  transition: box-shadow .15s;
  box-shadow: 0 0 0 3px #666;
}

.iconlogo {
  color: #fff;
  font-size: 3.6rem;
  font-weight: bold;
  position: relative;
  top: 1rem;
  left: 8rem;
}

.imglogo {
  width: 58px;
  position: absolute;
  top: -.5rem;
  left: -8rem;
}

.block--dark {
  color: #fff;
  clip-path: polygon(0 0, 100% 0%, 100% 90%, 0 100%);
