}

.block--dark-one .block__heading {
  color: #fff;
}

.block__heading-callout {
  text-align: center;
  font-size: 4rem;
  font-weight: bold;
}

.logopicture {
  width: 100%;
  padding-bottom: 8rem;
}

.container {
  flex-direction: column;
  display: flex;
}

.vtordelblock {
  margin-bottom: 12rem;
  padding: 0 4rem;
}

.inputsearch {
  width: 0;
  border: none;
  border-radius: 5rem;
  outline: none;
  flex-grow: 1;
}

.vtorheading {
  color: var(--color-headings);
  font-size: 4rem;
}

.block__paragraf-vtor {
  color: var(--color-body);
}

.badgevtordel {
  color: var(--color-headings);
}

.vtorlisti {
  flex-wrap: wrap;
  place-content: center;
  align-items: center;
  display: flex;
}

.badgevtordel:hover {
  background: var(--color-secondary);
  color: #fff;
}

.notick {
  color: #fff;
  list-style-image: none;
}

.features__block {
  row-gap: 2rem;
  padding: 4rem;
}

.feature__block-paragraf {
  color: var(--color-body);
  text-align: left;
}

.featuresimg, .showcaseimg {
  width: 100%;
}

.grid-show {
  padding: 6rem;
}

.showcaseheader {
  align-self: center;
  padding: 2rem 0;
}

.showcaseh3 {
  text-align: center;
}

.showcaseblock {
  padding-top: 2rem;
}

.mosh {
  padding: 0 18rem;
}

.media-other {
  color: var(--color-body);
  margin-bottom: 0;
  font-size: 2rem;
}

.media-image-mosh {
  width: 100%;
  display: inline-block;
}

.quotes-other {
  flex-direction: column;
  justify-items: center;
  font-size: 2rem;
  display: flex;
}

